import { css } from 'emotion'
import { useState } from 'react';

const LightBox = ({ onVisible, setOnVisible }) => {
  
  const baseStyle = css`
    visibility: ${onVisible ? 'visible' : 'collapse'};
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.7);
    z-index: 99;
    display: grid;
    justify-items: center;
    img {
      align-self: center;
      width: 50%;
      z-index: 100;
    }
  `

  return (
    <div className={ baseStyle }>
      <img src="/static/book1.jpg" alt=""/>
    </div>
  )
}

export default LightBox